<template>
  <div>vue</div>
  <v-form ref="form" @submit.prevent="send">
    <video id="video0"></video>
    <button @click="main()">tensor</button>
    <div>{{ resultgood }}</div>
  </v-form>
</template>

<script>
import axios from "axios";
export default {
  name: "app",
  data() {
    return {
      resultgood: "",
    };
  },
  methods: {
    main: async function () {
      const webcam = document.getElementById("video0");
      const cam = await tf.data.webcam(webcam);
      const img = await cam.capture();
      const mnet = await mobilenet.load();
      const result = await mnet.classify(img);
      this.resultgood =
        " :" + result[0].className + " :" + result[0].probability;
      img.print();
      tf.dispose(img);
      axios({
        url: "http://localhost:3000/about",
        method: "POST", //  post 
        data: {
          resultgood: this.resultgood,
        },
      }).then((res) => {
        alert(res.data.message);
      });
    },
  },
};
</script>

<style>
.video0 {
  width: 300px;
  height: 300px;
}
